'use client'
import Header from '@/components/Header'
import React, { useEffect, useState, useRef } from 'react'
import Viewer from '@/components/Viewer'
import { Model } from '@/components/types'
import StatusOverlay from '@/components/StatusOverlay'
export default function HomePage() {
  const [models, setModels] = useState<Model[]>([])
  const [selected, setSelected] = useState('')
  const [busy, setBusy] = useState(false)
  const [message, setMessage] = useState<string | null>(null)
  const viewer = useRef<any>(null)

  // fetch models
  useEffect(() => {
    fetch('/api/models').then(r => r.json()).then(setModels)
  }, [])

  // upload handler
  const handleUpload = async (file: File) => {
    setBusy(true)
    setMessage(`Uploading ${file.name}...`)
    const data = new FormData()
    data.append('model-file', file)
    if (file.name.endsWith('.zip')) {
      const entry = prompt('ZIP entry file?')!
      data.append('model-zip-entrypoint', entry)
    }
    await fetch('/api/models', { method: 'POST', body: data })
    setModels(await fetch('/api/models').then(r=>r.json()))
    setBusy(false)
    setSelected(file.name)
  }

  // poll & load
  useEffect(() => {
    let id: number
    const check = async () => {
      if (!viewer.current || !selected) return
      setMessage('Checking translation...')
      const res = await fetch(`/api/models/${selected}/status`)
      const st = await res.json()
      if (st.status === 'inprogress') {
        setMessage(`Translating (${st.progress})...`)
        id = window.setTimeout(check, 5000)
      } else if (st.status === 'failed') {
        setMessage('Translation failed')
      } else {
        setMessage(null)
        viewer.current.setLightPreset(0)
        window.Autodesk.Viewing.Document.load(
          'urn:'+selected,
          (doc: any) => viewer.current.loadDocumentNode(doc, doc.getRoot().getDefaultGeometry()),
          (_c, m) => console.error(m)
        )
      }
    }
    check()
    return () => clearTimeout(id)
  }, [selected])

  return (
    <div className="flex flex-col h-screen">
      <Header busy={busy} models={models} selected={selected} onSelect={setSelected} onUpload={handleUpload} />
      {message && <StatusOverlay message={message} />}
      <Viewer onViewerReady={v => (viewer.current = v)} />
    </div>
  )
}