'use client'
import React, { useEffect, useRef } from 'react'

interface TokenResponse { access_token: string; expires_in: number }
interface Props { onViewerReady: (v: any) => void }

declare global { interface Window { Autodesk: any } }

const Viewer: React.FC<Props> = ({ onViewerReady }) => {
  const ref = useRef<HTMLDivElement>(null)
  useEffect(() => {
    const s = document.createElement('script')
    s.src = 'https://developer.api.autodesk.com/modelderivative/v2/viewers/7.*/viewer3D.js'
    s.onload = () => {
      if (!ref.current) return
      async function init() {
        const getToken = (cb: (t: string, e: number) => void) => {
          fetch('/api/auth/token')
            .then(r => r.json())
            .then((d: TokenResponse) => cb(d.access_token, d.expires_in))
        }
        window.Autodesk.Viewing.Initializer(
          { env: 'AutodeskProduction', getAccessToken: getToken },
          () => {
            const v = new window.Autodesk.Viewing.GuiViewer3D(ref.current!, {})
            v.start()
            onViewerReady(v)
          }
        )
      }
      init()
    }
    document.head.appendChild(s)
  }, [onViewerReady])
  return <div ref={ref} className="flex-1 relative" />
}

export default Viewer